*{
  margin: 0;
  padding: 0;
}
#navbar{
  position: sticky;
  top: 0;
  width: 100%;
  display: flex;
  background-color: rgba(0, 0, 0, 0.8);
  color : #ffffff;
  padding-inline: 2rem;
  align-items: center;
  box-sizing: border-box;
  z-index: 999;
  column-gap: 10.5rem;
}
#logo{
  margin-inline: 0.5rem;
  margin-block: 0.5rem;
}
.nav-first{
  width: 26%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  float: left;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-weight: 400;
}
.nav-second{
  display: flex;
  width: 58%;
  align-items: center;
}
#nav-ul{
  display: flex;
  list-style: none;
}
#nav-ul li{
  text-decoration: none;
  margin-inline: 2.2rem;
}
#nav-ul li a{
  text-decoration: none;
  color : #ffffff;
}
#nav-ul li:hover{
  border-bottom: 2px solid white;
}
#btn{
  width: 25%;
  height: 2.5rem;
  border-radius: 1rem;
  font-size: 1rem;
  text-transform: capitalize;
  background-image: linear-gradient(to right , rgba(0,71,255,1),rgba(204,0,255,1));
  color: #ffffff;
  font-weight: lighter;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: none;
  word-break: break-all;
  transition: 0.2s ease-in-out;
margin-inline: 1.5rem;
}

#btn:hover{
  transform: scale(1.02);
}
.frame2{
  width: 100%;
  box-sizing: border-box;
  padding-block: 2rem;
}
.frame2 h3{
  font-size: 1.5rem;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-weight: 400;
  text-align: center;
  margin-bottom: 2rem;
  /* border-bottom: 2px solid orangered; */
  
}
.images{
  width: 90%;
  margin-inline: 5%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  row-gap: 2rem;
  column-gap: 7rem;
}
 
.images img{
  height: 5rem;
}
.main-footer{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding-top: 4rem;
  background-color: rgba(0, 0, 0,1);
  color: #ffffff;
  border: none;
}
.main-left{
  width: 40%;
}
.main-left h3{
  font-weight: 500;
  font-size: 1.25rem;
}
.main-left .address{
  font-size: 1rem;
  color: #ffffff;
  text-transform: capitalize;
  width: 80%;
  display: flex;
  gap: 4rem;
  margin-block: 1rem;
  line-height: 2rem;
}
.main-left .address h5{
  font-weight: 300;
  
}
.social-logos{
width: 60%;
display: flex;
flex-wrap: wrap;
height: 2rem;
gap: 1.25rem;
}
.social-logos img{
  height: 1.75rem;
}

.main-right{
  width: 40%;
  height: 13.5rem
  
}
.main-right iframe{
  width: 100%;
  height: 100%;
}
#closing{
  background-color: black;
  color: #ffffff;
  text-align: center;
  border: none;
  font-weight: 300;
  font-size: 1rem;
  margin-block: 4rem;
}

.hamburger{
  width: 10%;
  display: flex;
  flex-direction: column;
  row-gap: 0.4rem;
  height: 2rem;
  display: none;
  margin-left: 13%;
}
.hamburger .bar{
  width: 100%;
  background-color: white;
  border: none;
  height: 0.4rem;
  display: block;
}
@media (min-width : 1024px) and (max-width : 1200px){
#navbar{
  column-gap: 2.5rem;
}
.nav-first{
  width: 29%;
}

.nav-second{
  width: 66%;
}
}
@media (min-width : 769px) and (max-width : 1023px){
  #navbar{
    column-gap: 2.5rem;
  }
  .nav-first{
    width: 29%;
  }
  .nav-first .heading{
    font-size: 0.6rem;
  }
  .nav-second{
    width: 66%;
  }
  #nav-ul li{
    margin-inline: 1rem;
  }
  }
  @media (min-width : 480px) and (max-width : 768px){
    #navbar{
      column-gap: 2rem;
      padding-inline: 1rem;
    }
    .nav-first{
      width: 35%;
      flex-wrap: nowrap;
    }
    .nav-first img{
      scale: (0.8);
      margin-inline: 0.25rem;
    }
    .nav-first .heading{
      font-size: 0.6rem;
    }
    .nav-second{
      width: 25%;
    }
    #nav-ul {
      display: flex;
      list-style: none;
      flex-direction: column;
      row-gap: 2rem;
      position: absolute;
      top: 100%;
      right: 0;
      background-color: black;
      padding-inline: 2rem;
      box-sizing: border-box;
      padding-block: 2rem;
      transition: 0.2s ease-in;
      
  }
  .hamburger{
    display: flex;
  }
  #btn{
    margin-inline: 0;
    width: 100%;
  }
  .main-left .address{
    gap: 2rem;
    line-height: 1rem;
    font-size: 0.75rem;
  }
  .social-logos{
    gap: 1rem;
    height: 1.5rem;
  }
    }

  @media (min-width : 325px) and (max-width : 479px) {
    #navbar{
      column-gap: 1rem;
      padding-inline: 1rem;
    }
    .nav-first{
      width: 39%;
      flex-wrap: nowrap;
    }
    .nav-first img{
      scale: (0.8);
      margin-inline: 0.25rem;
    }
    .nav-first .heading{
      font-size: 0.6rem;
    }
    .nav-second{
      width: 35%;
    }
    #nav-ul {
      display: flex;
      list-style: none;
      flex-direction: column;
      row-gap: 2rem;
      position: absolute;
      top: 100%;
      right: 0;
      background-color: black;
      padding-inline: 1rem;
      box-sizing: border-box;
      padding-block: 2rem;
      transition: 0.2s ease-in;
      
  }
  .hamburger{
    display: flex;
    margin-left: 4%;
  }
  #btn{
    margin-inline: 0;
    width: 100%;
    height: 2rem;
    font-size: 0.7rem;
  }
  .images img{
    height: 3rem;
  }
  .main-footer{
    padding-top: 2rem;
  }
  .main-left .address{
    gap: 1rem;
    line-height: 0.75rem;
    font-size: 0.5rem;
  }
  .social-logos{
    gap: 0.5rem;
    height: 1rem;
    flex-wrap: nowrap;
  }
  .social-logos img{
    height: 1.25rem;
  }
  }